@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.menu2 {
  margin-top: 10rem;

  display: flex;
  flex-direction: column;
}

.avatar {
  display: flex;
  flex-direction: row-reverse;
  align-items: flex-end;
}

a {
  max-width: 10rem;
  font-weight: bold;
  border-radius: 0.6rem;
  text-align: center;

  color: #ccc6ce;
  text-decoration: none;
  margin-top: 1.5rem;

  transition: 0.7s;
  align-items: center;
}

a:hover {
  background-color: #7bf021;
  color: currentColor;
}

.menu {
  display: grid;

  grid-template-columns: 2fr 1fr;

  height: 100%;

  justify-content: left;
  font-size: 3vw;
  padding: 4rem;
}

.foto-perfil {
  height: 6rem;
  width: 6rem;

  border-radius: 50%;
}

* {
  padding: 0px;
  margin: 0px;
  outline: 0;
  box-sizing: border-box;
}

body,
html {
  height: 100%;
  font: 480 14px roboto, sans-serif;
  background-image: url(/portfolio/static/media/img2.a1342be7.jpg);
  background-position: center;
}

